<template>
  <section class="new-partners">
    <div class="container">
      <TheTitle :theme="theme" :title="title" />
      
      <swiper
        class="parallax-slider"
        parallax
        grabCursor
        pagination
        :navigation="{ nextEl: '.nextArrow', prevEl: '.prevArrow' }"
        :slides-per-view="slidesToShow"
      >
        <div class="parallax-slider-navigation">
          <div class="nav-indicator nextArrow">
            <i class="fas fa-chevron-right"></i>
          </div>
          <div class="nav-indicator prevArrow">
            <i class="fas fa-chevron-left"></i>
          </div>
        </div>
        
        <swiper-slide v-for="image in images" :key="image.id">
          <img :src="image.imageUrl" />
        </swiper-slide>
    </swiper>
    </div>
  </section>
</template>
<script>
  import TheTitle from '../../components/The-title.vue'
// Import Swiper Vue.js components
  import SwiperCore, {Navigation, Parallax, Pagination} from 'swiper'
  import { Swiper, SwiperSlide } from 'swiper/vue'
  import 'swiper/swiper.scss';
  import 'swiper/components/navigation/navigation.scss'
  import 'swiper/components/pagination/pagination.scss'
  
  SwiperCore.use([Navigation, Parallax, Pagination])

export default {
  name: 'New-partners',
  components: {
    TheTitle,
    Swiper,
    SwiperSlide,
  },
  data() {
    return {
      images: [
        {
          id: 0,
          imageUrl: require('../../assets/images/partners/aeomed.png')
        },
        {
          id: 1,
          imageUrl: require('../../assets/images/partners/axion.png')
        },
        {
          id: 2,
          imageUrl: require('../../assets/images/partners/genoray.png')
        },
        {
          id: 3,
          imageUrl: require('../../assets/images/partners/tecno.png')
        },
        {
          id: 4,
          imageUrl: require('../../assets/images/partners/aeomed.png')
        },
        {
          id: 5,
          imageUrl: require('../../assets/images/partners/axion.png')
        },
        {
          id: 6,
          imageUrl: require('../../assets/images/partners/genoray.png')
        },
        {
          id: 7,
          imageUrl: require('../../assets/images/partners/tecno.png')
        },
      ],
      parallaxSwiperWidth: 0,
      theme: 'Partners',
      title: 'Our new partners',
      slidesToShow: '4',
      windowWidth: null,
    }
  },
  methods: {
    checkScreen() {
      this.windowWidth = window.innerWidth
      if(this.windowWidth <= 1430 && this.windowWidth > 1020) {
        this.slidesToShow = 3
        return
      }
      if(this.windowWidth <= 1020 && this.windowWidth > 790) {
        this.slidesToShow = 2
        return
      }
      if(this.windowWidth <= 790) {
        this.slidesToShow = 1
        return
      }
      this.slidesToShow = 4
      return
    },
  },
  mounted() {
    window.addEventListener('resize', this.checkScreen)
    this.checkScreen()
  },
}
</script>


<style lang="scss">
  .new-partners{
    padding: 50px 0;
    .swiper-wrapper{
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-top: 50px;
      
      .swiper-slide{
        margin: 0;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        min-height: 100px;
      }  
    }
    .parallax-slider-navigation{
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      z-index: 999;
      cursor: default;
      
      .prevArrow{
        order: 1;
        font-size: 45px;
        margin: 0 100px;
        cursor: pointer;
      }
      .nextArrow{
        order: 2;
        font-size: 45px;
        margin: 0 100px;
        cursor: pointer;
      }
    }
    
    .swiper-pagination{
      height: 40px;
    }
  }
</style>